	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Car Bites Tree","date":"16 Feb 2002","slug":"/2002-02-16-car-bites-tree/","tags":"Y2002"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/02/carWithTree.jpg\" height=\"107\" width=\"143\" border=\"0\" alt=\"carWithTree.jpg: \" align=\"left\" /> This is <b>not</b> how my encounter with the neighbor's tree ended up yesterday, but it's close. His tree was actually much taller, but the part that hit the road was about this size.</p>"},"next":{"fields":{"title":"Head Spins","date":"15 Feb 2002","slug":"/2002-02-15-head-spins/"}},"previous":{"fields":{"title":"Cranky","date":"17 Feb 2002","slug":"/2002-02-17-cranky/"}}},{"node":{"fields":{"title":"Head Spins","date":"15 Feb 2002","slug":"/2002-02-15-head-spins/","tags":"Y2002"},"html":"<p>Christopher woke us up at 2am, thirsty and with fever. He's been healthy for so long (except while we were in Minnesota; in Germany he hasn't missed any daycare in over a year), we've almost forgotten what it's like for him to be sick. For the first time, he wanted to come to our bed. We let him fall asleep there, then brought him back down. We don't want to start anything.  </p>\n<p>Of course, by the time I got back to sleep it was time to get up already, so I could have just as well skipped it.</p>\n<hr />\nChristopher and I just had a scare. We were almost home from daycare, already in Lüllau. I could see our neighbor was cutting trees, tall ones. What I didn't know was that the tree being cut right then was half rotted and was falling in the wrong direction... toward the highway! It landed right in front of my car. It didn't touch us, we were able to stop in time, and we weren't hurt. I'm sure our neighbor was more shocked and surprised than we were.</p>"},"next":{"fields":{"title":"Four More","date":"15 Feb 2002","slug":"/2002-02-15-four-more/"}},"previous":{"fields":{"title":"Car Bites Tree","date":"16 Feb 2002","slug":"/2002-02-16-car-bites-tree/"}}},{"node":{"fields":{"title":"Four More","date":"15 Feb 2002","slug":"/2002-02-15-four-more/","tags":"Y2002"},"html":"<p>Just when we thought all the NPD informants were known, now Spiegel reports <a href=\"http://www.spiegel.de/politik/deutschland/0,1518,182412,00.html\">Und noch mehr V-Leute</a> - and still more infomants. Four more. It's now estimated that 10% of the NPD leadership were actually working for the government.</p>"},"next":{"fields":{"title":"A Place To Start","date":"15 Feb 2002","slug":"/2002-02-15-a-place-to-start/"}},"previous":{"fields":{"title":"Head Spins","date":"15 Feb 2002","slug":"/2002-02-15-head-spins/"}}}],"pathPrefix":"","first":false,"last":false,"index":926,"pageCount":1101}}

